.footer {

  display: flex;
  padding: 50px 0;
  font-family: "IBM Plex Sans", sans-serif;
  font-size: 14px;
  font-weight: 400;

  &__media {

    display: flex;
    flex-basis: 20%;
    flex-direction: column;

    align-items: center;

    &-share {

      width: 24px;
      height: 24px;
      margin: 0 5px;

    }

    &-logo {

      width: 63px;
      height: 72px;
      padding-bottom: 62px;

    }
  }

  &__menu {

    display: flex;
    flex-basis: 45%;
  }

  &__contacts {

    display: flex;
    flex-basis: 35%;
    border-right: 1px solid rgba(26, 90, 76, 0.5);
    padding-right: 20px;

  }

  &__left,
  &__right {

    flex-basis: 50%;

  }

  &__time {

    margin: 0;

  }

  &__list {

    list-style: none;

  }

  &__item {

    padding: 10px 0;

  }

  &__link,
  &__link:visited {

    text-decoration: none;
    color: #333;

  }
}
